import lan  from '../../static/js/language'
import test from '../../static/js/sex'
import Chat from '../public/chat'
import Loading from '../public/Loading'
import Head from '../public/head'
import loadingImg from '../public/loadingImg'
import none from '../public/noNumber'
  export default {
      name: "myPlay",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          year:(new Date()).getFullYear(),
          month:(new Date()).getMonth() + 1,
          day:(new Date()).getDate(),
          todayYear:(new Date()).getFullYear(),
          todayMonth:(new Date()).getMonth() + 1,
          dayCount:[],
          color:[],
          activeList:[],
          time:[],
          downIndex:'',
          upIndex:'',
          chooseTime:[],
          dateActive:[],
          isLoading:false,
          isDate:true,
          isInvite:false,
          date:[],
          datePicker:[],
          timePicker:[],
          dateTime:[],
          timeAll:[],
          checked:false,
          active_id:"",
          clickIndex:1,
          lookIndex:1,
          completList:[],
          total:0,
          isdiscus:false,
          content:'',
          order_id:'',
          userName:'',
          remakeTime:'',
          index:1,
          isADate:true,
          clickArr:[
            {name:'活动日历',index:1},
            {name:'活动预定',index:2},
            {name:'志愿者申请',index:3},
            {name:'退款申请',index:4},
            {name:'已完成',index:5},
          ],
          orderActive:[],
          timeContent:'',
          isChat:false,
          imgUrl:'',
          userid:'',
          to_user_id:'',
          serchList:[
            {name: '全部'},
            {name: '已付款'},
            {name: '未付款'},
            {name:'已主动退款'}
          ],
          ListIndex:0,
          orderList:[
            {name:'活动时间从早到晚',index:1},
            {name:'活动时间从晚到早',index:-1},
            {name:'下单时间升序',index:2},
            {name:'下单时间降序',index:-2},
          ],
          Isorder:false,
          orderIndex:0,
          ordertotal:0,
          volunList:[],
          voluntotal:0,
          languageList:[],
          sexArr:[],
          refundList:[],
          refundtotal:0,
          lookHouse:false,
          houseList:[],
          lookHouseB:false,
          houseListB:[],
          orderSort:1,
          complete_num: '',
          enroll_num: '',
          order_num: '',
          refund_num: '',
          voluncontent:'',
          paginationShow:true
        }
    },
    components:{
        Head,
      Loading,
      loadingImg,
      none,
      Chat,
    },
    watch:{
      year:function(){
        this.chooseTime = []
        this.getMonth()
        this.getDateActive()
      },
      month:function(){
        this.chooseTime = []
        this.getMonth()
        this.getDateActive()
      },
      isInvite:function() {
        if (!this.isInvite) {
          this.date = []
          this.dateTime = []
          this.checked = false
        }
      },
      checked:function(){
        if(this.checked){
          this.getAlltime()
        }else{
          this.date = []
          this.dateTime = []
        }
      },
      clickIndex:function(){
        this.index = 1
        if(this.clickIndex == 5){
          this.getAll(1)
          this.doAllRead(4)
        }else if(this.clickIndex == 2){
          this.getOrid(1,'',this.orderSort)
          this.doAllRead(1)
        }else if(this.clickIndex == 3){
          this.getEroll(1)
          this.doAllRead(2)
        }else if(this.clickIndex == 4){
          this.getRefund(1)
          this.doAllRead(3)
        }
      },
      orderIndex:function(){
        this.index = 1
        if(this.ListIndex == '1'){
          this.getOrid(1,1,this.orderSort,0,)
        }else if(this.ListIndex == '0'){
          this.getOrid(1,'',this.orderSort)
        }else if(this.ListIndex == '2'){
          this.getOrid(1,0,this.orderSort)
        }else{
          this.getOrid(1,1,this.orderSort,2,'',1)
        }
      }
    },
    methods:{
        doAllRead(flag){
          this.$http.post(this.api + '/PlannerReadAll',{
            token: localStorage.getItem('token'),
            flag:flag
          })
            .then(res=>{
              if(res.data.code == 1){
                if(flag == 1){
                  this.order_num = 0
                }else if(flag == 2){
                  this.enroll_num = 0
                }else if(flag == 3){
                  this.refund_num = 0
                }else{
                  this.complete_num =0
                }
              }else if(res.data.code == 3){
                this.doAllRead(flag)
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        },
        getNoReadAll(){
          this.$http.post(this.api + '/PlannerNoRead',{
            token: localStorage.getItem('token'),
          })
            .then(res=>{
              if(res.data.code == 1){
                let data = res.data.data
                this.complete_num =data.complete_num
                  this.enroll_num = data.enroll_num
                  this.order_num = data.order_num
                  this.refund_num = data.refund_num
              }else if(res.data.code == 3){
                this.getNoReadAll()
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        },
      toPublish(item,index){
        this.$router.push({
          path: '/publishPage',
          query: {
            information: item.activity_id
          }
        })
      },
      changeErollAB(item,val){
        if(val == 3){
          this.$router.push({
            path: '/refundDescirbe',
            query:{
              information: item.refund_id,
              type:3,
              index:'4'
            }
          })
        }
      },
      lookXqB(item,index){
        this.lookHouseB = !this.lookHouseB
        this.houseListB = item.house
      },
      lookXq(item,index){
        this.lookHouse = !this.lookHouse
        this.houseList = item.house
      },
      handleRefundChange(val){
        this.getRefund(val)
      },
      getRefund(val){
        this.isLoading = true
        this.$http.post(this.api + '/RefundLPlanner',{
          token: localStorage.getItem('token'),
          page: val,
        })
          .then(res=>{
            if(res.data.code == 1){
              this.refundList = res.data.data.data
              this.refundtotal = res.data.data.total
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getRefund(val)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isLoading = false
            }
          })
      },
      changeVolunErollA(item,val){
        this.isLoading = true
        this.$http.post(this.api + '/ErollA',{
          token: localStorage.getItem('token'),
          enroll_id: item.enroll_id,
          flag: val
        })
          .then(res=>{
            if(res.data.code == 1){
              item.audit = val == 1? 1:2
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  if(res.data.code == 1){
                    localStorage.setItem('token',res.data.data)
                    this.changeVolunErollA(item,val)
                  }
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isLoading = false
            }
          })
      },
      handlevolunChange(val){
        this.getEroll(val)
      },
      getEroll(val){
        this.isLoading = true
        this.$http.post(this.api + '/ErollL',{
          token: localStorage.getItem('token'),
          page: val
        })
          .then(res=>{
            if(res.data.code == 1){
              this.volunList = res.data.data.data
              this.voluntotal = res.data.data.total
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  if(res.data.code == 1){
                    localStorage.setItem('token',res.data.data)
                  }
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isLoading = false
            }
          })
      },
      handleOrderChange(val){
          this.index = val
        this.paginationShow = false
        if(this.ListIndex == '1'){
          this.getOrid(val,1,this.orderSort,0,)
        }else if(this.ListIndex == '0'){
          this.getOrid(val,'',this.orderSort)
        }else if(this.ListIndex == '2'){
          this.getOrid(val,0,this.orderSort)
        }else{
          this.getOrid(1,1,this.orderSort,2,'',1)
        }
        this.$nextTick(function () {
          this.paginationShow = true;
        })
      },
      changeOrder(items,indexs){
        this.orderIndex = indexs
        this.orderSort = items.index
        this.Isorder = false
      },
      orderClick(){
        this.Isorder = !this.Isorder
      },
      changeClickList(items,indexs){
        this.ListIndex = indexs
        this.index = 1
        if(this.ListIndex == '1'){
          this.getOrid(1,1,this.orderSort,0,)
        }else if(this.ListIndex == '0'){

          this.getOrid(1,'',this.orderSort)
        }else if(this.ListIndex == '2'){

          this.getOrid(1,0,this.orderSort)
        }else{

          this.getOrid(1,1,this.orderSort,2,'',1)
        }
      },
      toRefundDes(item){
        this.$router.push({
          path: '/refundDescirbe',
          query:{
            information: item.order_id,
            type:1,
            index: 2
          }
        })
      },
      chat(){
        this.isChat = false
      },
      changeErollA(item,val){
        if(val == 2){
          this.$router.push({
            path:'/playerReturn',
            query:{
              information: item.order_id
            }
          })
        }else if(val == 3){
          this.$router.push({
            path: '/refundDescirbe',
            query:{
              information: item.order_id,
              type:2
            }
          })
        }else{
          this.isChat = true
          this.to_user_id = item.user.user_id
          this.userName = item.user.family_name+item.user.middle_name+item.user.name?item.user.family_name+item.user.middle_name+item.user.name:'匿名用户'
          this.other_img = item.user.head_image?item.user.headimage.domain+item.user.headimage.image_url:'../../../static/img/static/user.png'
        }
      },
      takeUp(item,index){
        this.timeContent = item.activ_begin_time +'—'+ item.activ_end_time
      },
      takevolunUp(item,index){
        let a = ''
        let b = this.languageList[item.main_language].label + ','
        for(let i = 0;i<item.language.length;i++){
          a += this.languageList[item.language[i]].label+','
        }
        a = a +'/'+ item.skill
        this.voluncontent =b+ a
      },
        //获取预定
      getOrid(val,flag,sort,status,iscomplete,type){
        this.$http.post(this.api + '/OrderLPlanner',{
          token: localStorage.getItem('token'),
          flag: flag,
          page: val,
          status:status,
          iscomplete:iscomplete,
          sort:sort,
          type:type
        })
          .then(res=>{
            if(res.data.code == 1){
              this.orderActive = res.data.data.data
              this.ordertotal = res.data.data.total
            }else if(res.data.code == 3){

            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
        //查看详情
      lookMore(item){
        this.$router.push({
          path: '/refundDescirbe',
          query:{
            information: item.order_id,
            type:4,
            index: '5'
          }
        })
      },
      toRemake(item,index){
        this.$router.push({
          name: 'Experience',
          query:{
            information: item.activity_id,
            complete: item.complete
          }
        })
      },
      deleteActive(item,index){
        if(!this.chooseTime.length){
          this.isInvite = true
          this.remakeTime = 1
          this.active_id = item.activity_id
          this.getTime(item.activity_id)
        }else{
          this.$confirm('此操作将删除当天的这个活动, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.deleteSingle(item)
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      deleteSingle(val){
        this.$http.post(this.api + '/SlotD',{
          token: localStorage.getItem('token'),
          slot_id: val.slot_id
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                type: 'success',
                message: '删除成功！'
              })
              this.getActive()
              this.getDateActive()
            }else if(res.data.code == 3){
              this.deleteSlot()
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      saveDiscuss(){
        if( !this.content){
          this.$message({
            type: 'info',
            message: '请填写评价'
          })
        }else{
          this.$http.post(this.api + '/CommentPlannerS',{
            token: localStorage.getItem('token'),
            content: this.content,
            order_id: this.order_id
          })
            .then(res=>{
              if(res.data.code == 1){
                this.$message({
                  type: 'success',
                  message: '评价成功!'
                })
                this.content = ''
                this.isdiscus = false
                if(this.lookIndex == 1){
                  this.getAll(1)
                }else{
                  this.getNocomplete(1,this.lookIndex -1)
                }
              }else if(res.data.code == 3){
                this.saveDiscuss()
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }

      },
      toDiscuss(item){
        this.order_id = item.order_id
        this.userName = item.user.family_name+item.user.middle_name+item.user.name
        this.isdiscus = !this.isdiscus
      },
      closeDiscus(){
        this.isdiscus = false
        this.content = ''
      },
      handleCurrentChange(val){
        if(this.lookIndex == 1){
          this.getAll(val)
        }else{
          this.getNocomplete(val,this.lookIndex -1)
        }
      },
      getAll(val){
        this.isLoading = true
        this.$http.post(this.api + '/OrderLPlanner',{
          token: localStorage.getItem('token'),
          page: val,
          flag: 1,
          iscomplete: 2,
          status:0
        })
          .then(res=>{
            if(res.data.code == 1){
              this.total = res.data.data.total
              this.completList = res.data.data.data
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getAll(val)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isLoading = false
            }
          })
      },
        getNocomplete(val,num){
          this.isLoading = true
          this.$http.post(this.api + '/OrderLPlanner',{
            token: localStorage.getItem('token'),
            page: val,
            flag: 1,
            isevaluate_planner:num,
            iscomplete: 2,
            status:0
          })
            .then(res=>{
              if(res.data.code == 1){
                this.total = res.data.data.total
                this.completList = res.data.data.data
                this.isLoading = false
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
                this.isLoading = false
              }
            })
        },
      changeLook(val){
        if(this.lookIndex != val){
          this.lookIndex = val
        }
        if(val == 2){
          this.getNocomplete(1,this.lookIndex -1)
        }else if(val == 3){
          this.getNocomplete(1,this.lookIndex-1)
        }else{
          this.getNocomplete(1)
        }
      },
      changeClick(val){
       if(this.clickIndex != val){
         this.clickIndex = val
       }
      },
      toApplication(item,index){
        this.$router.push({
          name:'appliCation',
          query:{
            information: item.activity_id
          }
        })
      },
        getMonth(){
          let lastDay = this.getLastDay(this.year,this.month)
          let day = this.getFirtDay(this.year,this.month -1)
          let temp = []
          let dayCount = []
          let color = []
          let b = []
          for(let i =1;i<day;i++){
            temp.push(' ')
            color.push({index:0})
          }
          for(let j= 1;j<=lastDay;j++){
            temp.push(j)
            b.push({index:0})
            if(this.time.length){
              for(let m = 0;m<this.time.length;m++){
                if(this.time[m].long_day){
                  //单天活动
                  for(let n = 0;n<this.time[m].slot.length;n++){
                    if(new Date(this.year,this.month-1,j).getTime() == this.parseDate(this.time[m].slot[n].day)){
                      if(new Date(this.year,this.month-1,j).getTime() == new Date(this.todayYear,this.todayMonth-1,this.day).getTime()){
                        b[j-1] = ({today:1})
                      }
                      for(let l = 0;l<this.time[m].slot[n].list.length;l++){
                        if(this.time[m].slot[n].list[l].enroll_count !=0|| this.time[m].slot[n].list[l].order_num !=0|| this.time[m].slot[n].list[l].refund_num !=0){
                          if(this.time[m].slot[n].list[l].online == 1){
                            if( b[j-1].today){
                              b[j-1] = ({index:1,flag:1,today:1,online:1})
                            }else{
                              b[j-1] = ({index:1,flag:1,online:1})
                            }
                          }else{
                            if(b[j-1].online == 1){
                              if( b[j-1].today){
                                b[j-1] = ({index:1,flag:1,today:1,online:1})
                              }else{
                                b[j-1] = ({index:1,flag:1,online:1})
                              }
                            }else{
                              if( b[j-1].today){
                                b[j-1] = ({index:1,flag:1,today:1})
                              }else{
                                b[j-1] = ({index:1,flag:1})
                              }
                            }
                          }
                        }else{
                          if(this.time[m].slot[n].list[l].online == 1){
                            if(b[j-1].flag){
                              if( b[j-1].today){
                                b[j-1] = ({index:1,flag:1,today:1,online:1})
                              }else{
                                b[j-1] = ({index:1,flag:1,online:1})
                              }
                            }else{
                              if( b[j-1].today){
                                b[j-1] = ({index:1,today:1,online:1})
                              }else{
                                b[j-1] = ({index:1,online:1})
                              }
                            }
                          }else{
                            if(b[j-1].online){
                              if(b[j-1].flag){
                                if( b[j-1].today){
                                  b[j-1] = ({index:1,flag:1,today:1,online:1})
                                }else{
                                  b[j-1] = ({index:1,flag:1,online:1})
                                }
                              }else{
                                if( b[j-1].today){
                                  b[j-1] = ({index:1,today:1,online:1})
                                }else{
                                  b[j-1] = ({index:1,online:1})
                                }
                              }
                            }else{
                              if(b[j-1].flag){
                                if( b[j-1].today){
                                  b[j-1] = ({index:1,flag:1,today:1})
                                }else{
                                  b[j-1] = ({index:1,flag:1})
                                }
                              }else{
                                if( b[j-1].today){
                                  b[j-1] = ({index:1,today:1})
                                }else{
                                  b[j-1] = ({index:1})
                                }
                              }
                            }
                          }

                        }
                      }
                    }else{
                      if( b[j-1].index == 1){
                        b[j-1].index = 1
                      }else{
                        b[j-1] = ({index:2})
                      }
                    }
                  }
                }else{
                  //多天活动
                  for(let n = 0;n< this.time[m].slot.length;n++){
                    if(new Date(this.year,this.month -1,j).getTime() >= this.parseDate(this.time[m].slot[n].begin_date) && new Date(this.year,this.month -1,j).getTime() <= this.parseDate(this.time[m].slot[n].end_date)){
                      if(new Date(this.year,this.month-1,j).getTime() == new Date(this.todayYear,this.todayMonth-1,this.day).getTime()){
                        b[j-1] = ({today:1})
                      }
                      if(this.time[m].slot[n].enroll_count !=0|| this.time[m].slot[n].order_num !=0|| this.time[m].slot[n].refund_num !=0){
                        if(this.time[m].slot[n].online == 1){
                          if( b[j-1].today){
                            b[j-1] = ({index:1,flag:1,today:1,online:1})
                          }else{
                            b[j-1] = ({index:1,flag:1,online:1})
                          }
                        }else{
                          if(b[j-1].online){
                            if( b[j-1].today){
                              b[j-1] = ({index:1,flag:1,today:1,online:1})
                            }else{
                              b[j-1] = ({index:1,flag:1,online:1})
                            }
                          }else{
                            if( b[j-1].today){
                              b[j-1] = ({index:1,flag:1,today:1})
                            }else{
                              b[j-1] = ({index:1,flag:1})
                            }
                          }
                        }
                      }else{
                        if(this.time[m].slot[n].online == 1){
                          if(b[j-1].flag){
                            if( b[j-1].today){
                              b[j-1] = ({index:1,flag:1,today:1,online:1})
                            }else{
                              b[j-1] = ({index:1,flag:1,online:1})
                            }
                          }else{
                            if( b[j-1].today){
                              b[j-1] = ({index:1,today:1,online:1})
                            }else{
                              b[j-1] = ({index:1,online:1})
                            }
                          }
                        }else{
                          if(b[j-1].online){
                            if(b[j-1].flag){
                              if( b[j-1].today){
                                b[j-1] = ({index:1,flag:1,today:1,online:1})
                              }else{
                                b[j-1] = ({index:1,flag:1,online:1})
                              }
                            }else{
                              if( b[j-1].today){
                                b[j-1] = ({index:1,today:1,online:1})
                              }else{
                                b[j-1] = ({index:1,online:1})
                              }
                            }
                          }else{
                            if(b[j-1].flag){
                              if( b[j-1].today){
                                b[j-1] = ({index:1,flag:1,today:1})
                              }else{
                                b[j-1] = ({index:1,flag:1})
                              }
                            }else{
                              if( b[j-1].today){
                                b[j-1] = ({index:1,today:1})
                              }else{
                                b[j-1] = ({index:1})
                              }
                            }
                          }
                        }
                      }
                    }else{
                      if( b[j-1].index == 1){
                        b[j-1].index == 1
                      }else{
                        b[j-1] = ({index:2})
                      }
                    }
                  }
                }
              }
            }else{
              b[j-1] = ({index:2})
            }
          }
          this.dayCount = temp
          this.color = color.concat(b)
          this.isDate = false
        },
        getLastDay(year,month){
          let lastDay = new Date(year,month,0)
          let day = lastDay.getDate()
          return day
        },
        getFirtDay(year,month){
          let firstDay = new Date(year,month)
          let dayWeek = firstDay.getDay()
          if(dayWeek == 0){
            dayWeek = 7
          }
          return dayWeek
        },
      reduce(val){
          if(val == 1){
            this.year -= 1
          }else{
            if(this.month == 1){
              this.month = 12
              this.year -= 1
            }else{
              this.month -= 1
            }
          }
      },
      add(val){
          if(val == 1){
            this.year += 1
          }else{
            if(this.month == 12){
              this.month = 1
              this.year += 1
            }else{
              this.month += 1
            }
          }
      },
      getActive(){
        this.$http.post(this.api + '/ActivityList',{
          token: localStorage.getItem('token'),
        })
          .then(res=>{
            if(res.data.code == 1){
              if(res.data.data.length){
                this.activeList = res.data.data
                let a = []
                let c = ''
                for(let i = 0;i<this.activeList.length;i++){
                  a.push({long_day:this.activeList[i].long_day,slot:this.activeList[i].slot})
                }
                this.time = a
              }
              this.getMonth()
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/Index/token')
                .then(res=>{
                  if(res.data.code == 1){
                    localStorage.setItem('token',res.data.data)
                    this.getActive()
                  }
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      parseDate(str) { // 字符串转时间戳
        return Date.parse(new Date(Date.parse(str.replace(/-/g, "/")))) ;
      },
      chooseMore(item,index){
          this.downIndex = index
        if(this.color[index].index == 3){
          this.color[index].index = 1
          if(this.month < 10){
            var b  = '0'+ this.month
          }else{
            var b = this.month
          }
          if(item<10){
            var c  = '0' + item
          }else{
            var c = item
          }
          let d = this.year.toString()+'-'+ b+'-' + c
          let a = this.chooseTime.indexOf(d)
          this.chooseTime.splice(a,1)
        }else{
          this.color[index].index = 3

          if(this.month < 10){
            var a = '0'+ this.month
          }else{
            var a = this.month
          }
          if(item<10){
            var b = '0' + item
          }else{
            var b = item
          }
          this.chooseTime.push(this.year.toString()+'-'+ a+'-' + b)
        }
      },
      leaveMore(item,index){
        if(this.downIndex){
          if(this.color[index].index == 3){
            this.color[index].index = 1
            if(this.month < 10){
              var b  = '0'+ this.month
            }else{
              var b = this.month
            }
            if(item<10){
              var c  = '0' + item
            }else{
              var c = item
            }
            let d = this.year.toString()+'-'+ b+'-' + c
            let a = this.chooseTime.indexOf(d)
            this.chooseTime.splice(a,1)
          }else{
            this.color[index].index = 3
            if(this.month < 10){
              var a = '0'+ this.month
            }else{
              var a = this.month
            }
            if(item<10){
              var b = '0' + item
            }else{
              var b = item
            }
            this.chooseTime.push(this.year.toString()+'-'+ a+'-' + b)
          }
        }
      },
      dlete(item,index){
          this.downIndex = ''
        this.getDateActive()
      },
      getDateActive(){
        this.$http.post(this.api + '/ActivityList',{
          token: localStorage.getItem('token'),
          date:JSON.stringify(this.chooseTime)
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data
              let a = []
              if(this.chooseTime.length){
                for(let i =0;i< data.length;i++){
                  if(data[i].long_day){
                    for(let m=0;m<data[i].slot.length;m++){
                      for(let j = 0;j<data[i].slot[m].list.length;j++){
                        a.push({
                          time:data[i].slot[m].list[j].time[0]+'—'+data[i].slot[m].list[j].time[1],
                          price:data[i].slot[m].list[j].price,
                          title: data[i].title,
                          kind:data[i].kind,
                          activity_id:data[i].activity_id,
                          slot_id:data[i].slot[m].list[j].slot_id,
                          day:data[i].slot[m].day,
                          cover_image:data[i].cover_image,
                          cover:data[i].cover,
                          enroll_count:data[i].slot[m].list[j].enroll_count,
                          sale_num:data[i].slot[m].list[j].order_num,
                          is_doing:data[i].is_doing,
                          online:data[i].slot[m].list[j].online,
                          refund_num:data[i].slot[m].list[j].refund_num
                        })
                      }
                    }
                  }else{
                    for(let j=0;j<data[i].slot.length;j++){
                      a.push({
                        time:data[i].slot[j].begin_date+'—'+data[i].slot[j].end_date,
                        price:data[i].slot[j].price,
                        title: data[i].title,
                        kind:data[i].kind,
                        activity_id:data[i].activity_id,
                        slot_id:data[i].slot[j].slot_id,
                        cover_image:data[i].cover_image,
                        cover:data[i].cover,
                        enroll_count:data[i].enroll_count,
                        sale_num:data[i].slot[j].order_num,
                        is_doing:data[i].is_doing,
                        online:data[i].slot[j].online,
                        refund_num:data[i].slot[j].refund_num
                      })
                    }
                  }
                }
              }else{
                a = res.data.data
              }
              this.dateActive = a
              this.isADate = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getDateActive()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      //查看预定
      lookOrder(item,index){
        if(this.chooseTime.length){
          this.$router.push({
            path:'/LookOrder',
            query:{
              activeId: item.activity_id,
              slot_id:item.slot_id
            }
          })
        }else{
          this.$router.push({
            path:'/LookOrder',
            query:{
              activeId: item.activity_id
            }
          })
        }
      },
      //查看退款
      backMoney(item,index){
        if(this.chooseTime){
          this.$router.push({
            path: '/refundPage',
            query:{
              information: item.activity_id,
              slot_id: item.slot_id
            }
          })
        }else{
          this.$router.push({
            path: '/refundPage',
            query:{
              information: item.activity_id
            }
          })
        }

      },
      //邀请志愿者
      toInvite(item,index){
 /*       console.log(item)
        localStorage.setItem('invite_id',item.activity_id)*/
        this.$router.push({
          name: 'invitVolunter',
          query:{
            information: item.user_id,
            activeId:item.activity_id
          }
        })
      },
      //取消活动
      abolish(item,index){
        if(!this.chooseTime.length){
          this.isInvite = true
          this.remakeTime = 2
          this.active_id = item.activity_id
          this.getTime(item.activity_id)
        }else{
          this.$confirm('此操作将取消当天的这个活动, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.abolishSingle(item)
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }
      },
      abolishSingle(item){
        this.$http.post(this.api + '/SlotC',{
          token: localStorage.getItem('token'),
          activity_id: item.activity_id,
          flag: 2,
          is_all: 0,
          slot_id: item.slot_id
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                type: 'success',
                message: '取消成功！'
              })
              this.chooseTime = []
              this.getActive()
              this.getDateActive()
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.abolishTime()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      getTime(val){
        this.$http.post(this.api + '/home/Activity/get_activity',{
          token: localStorage.getItem('token'),
          activity_id: val,
          visit:0
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data
              if(data.long_day){
                this.Longday = true
                let a = []
                let c = []
                for(let i = 0;i<data.slot.length;i++){
                  c.push({index:i})
                  let b = []
                  for(let j = 0; j<data.slot[i].list.length;j++){
                    if(!data.slot[i].list[j].online){
                      b.push({label:data.slot[i].day +'  '+ data.slot[i].list[j].time[0]+'—'+data.slot[i].list[j].time[1],value:data.slot[i].list[j].slot_id})
                    }
                  }
                  if(b.length){
                    c[i].index = b
                    a.push({label:data.slot[i].day,value:i})
                  }
                }
                this.datePicker = a
                this.timeAll = c
              }else{
                this.Longday = false
                let a = []
                for(let i =0;i<data.slot.length;i++){
                  if(!data.slot[i].online){
                    a.push({label:data.slot[i].begin_date+' '+data.slot[i].begin_time+'—'+data.slot[i].end_date+' '+data.slot[i].end_time,value:data.slot[i].slot_id})
                  }
                }
                this.datePicker = a
              }
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getTime(val)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      abolishInvite(){
        if(this.remakeTime == 1){
          this.deleteSlot()
        } else{
          this.abolishTime()
        }
      },
      deleteSlot(){
        this.$http.post(this.api + '/SlotD',{
          token: localStorage.getItem('token'),
          slot_id: this.Longday?JSON.stringify(this.dateTime):JSON.stringify(this.date)
        })
          .then(res=>{
            if(res.data.code == 1){
              this.isInvite = false
              this.$message({
                type: 'success',
                message: '删除成功！'
              })
              this.getActive()
              this.getDateActive()
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.deleteSlot()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      abolishTime(){
        this.$http.post(this.api + '/SlotC',{
          token: localStorage.getItem('token'),
          activity_id: this.active_id,
          flag: 2,
          is_all: this.checked? 1:0,
          slot_id: this.Longday?JSON.stringify(this.dateTime):JSON.stringify(this.date)
        })
          .then(res=>{
            if(res.data.code == 1){
              this.isInvite = false
              this.$message({
                type: 'success',
                message: '取消成功！'
              })
              this.chooseTime = []
              this.getActive()
              this.getDateActive()
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.abolishTime()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      changeTime(event){
        let a = []
        for(let j = 0;j<this.date.length;j++){
          for(let i = 0;i<this.timeAll.length;i++){
            if(this.date[j] == i){
              for(let m = 0;m<this.timeAll[i].index.length;m++){
                a.push({label:this.timeAll[i].index[m].label,value:this.timeAll[i].index[m].value})
              }
            }
          }
        }
        this.timePicker = a
      },
      getAlltime(){
        let a = []
        let b = []
        let c = []
        for(let i = 0;i<this.datePicker.length;i++){
          a.push(this.datePicker[i].value)
        }
        for(let i = 0;i<this.timeAll.length;i++){
          for(let m = 0;m<this.timeAll[i].index.length;m++){
            c.push({label:this.timeAll[i].index[m].label,value:this.timeAll[i].index[m].value})
            b.push(this.timeAll[i].index[m].value)
          }
        }
        this.timePicker =  c
        this.date = a
        this.dateTime = b
      },
      //恢复活动
      restore(item,index){
        this.$http.post(this.api + '/SlotC',{
          token: localStorage.getItem('token'),
          activity_id: this.active_id,
          flag: 1,
          slot_id: item.slot_id
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                type:'success',
                message:'恢复活动成功！'
              })

              this.chooseTime = []
              this.getActive()
              this.getDateActive()
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.restore(item,index)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      reload(res){
        let data = res.data.data[0]
        this.userid = data.user_id
        this.imgUrl = data.head_image?data.headimage.domain + data.headimage.image_url:'../../../static/img/static/user.png'
      },
    },
    mounted(){
      this.getDateActive()
      this.getNoReadAll()
    },
    created(){
      this.getActive()
      this.languageList = lan()
      this.sexArr = test()
      if(this.$route.query.chooseIndex){
        this.clickIndex = this.$route.query.chooseIndex
      }
    }
  }



